import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import shangpinleixingList from '@/views/pages/shangpinleixing/list'
import shangpinleixingDetail from '@/views/pages/shangpinleixing/formModel'
import shangpinleixingAdd from '@/views/pages/shangpinleixing/formAdd'
import shangpinxinxiList from '@/views/pages/shangpinxinxi/list'
import shangpinxinxiDetail from '@/views/pages/shangpinxinxi/formModel'
import shangpinxinxiAdd from '@/views/pages/shangpinxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import cartList from '@/views/pages/shop_order/cart'
import addressList from '@/views/pages/shop_address/list'
import tehuihuodongList from '@/views/pages/tehuihuodong/list'
import tehuihuodongDetail from '@/views/pages/tehuihuodong/formModel'
import tehuihuodongAdd from '@/views/pages/tehuihuodong/formAdd'
import messagesList from '@/views/pages/messages/list'
import youjianList from '@/views/pages/youjian/list'
import youjianDetail from '@/views/pages/youjian/formModel'
import youjianAdd from '@/views/pages/youjian/formAdd'
import emailregistercodeList from '@/views/pages/emailregistercode/list'
import emailregistercodeDetail from '@/views/pages/emailregistercode/formModel'
import emailregistercodeAdd from '@/views/pages/emailregistercode/formAdd'
import menuList from '@/views/pages/menu/list'
import menuDetail from '@/views/pages/menu/formModel'
import menuAdd from '@/views/pages/menu/formAdd'
import discussshangpinxinxiList from '@/views/pages/discussshangpinxinxi/list'
import discussshangpinxinxiDetail from '@/views/pages/discussshangpinxinxi/formModel'
import discussshangpinxinxiAdd from '@/views/pages/discussshangpinxinxi/formAdd'
import discusstehuihuodongList from '@/views/pages/discusstehuihuodong/list'
import discusstehuihuodongDetail from '@/views/pages/discusstehuihuodong/formModel'
import discusstehuihuodongAdd from '@/views/pages/discusstehuihuodong/formAdd'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'shangpinleixingList',
			component: shangpinleixingList
		}, {
			path: 'shangpinleixingDetail',
			component: shangpinleixingDetail
		}, {
			path: 'shangpinleixingAdd',
			component: shangpinleixingAdd
		}
		, {
			path: 'shangpinxinxiList',
			component: shangpinxinxiList
		}, {
			path: 'shangpinxinxiDetail',
			component: shangpinxinxiDetail
		}, {
			path: 'shangpinxinxiAdd',
			component: shangpinxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'tehuihuodongList',
			component: tehuihuodongList
		}, {
			path: 'tehuihuodongDetail',
			component: tehuihuodongDetail
		}, {
			path: 'tehuihuodongAdd',
			component: tehuihuodongAdd
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'youjianList',
			component: youjianList
		}, {
			path: 'youjianDetail',
			component: youjianDetail
		}, {
			path: 'youjianAdd',
			component: youjianAdd
		}
		, {
			path: 'emailregistercodeList',
			component: emailregistercodeList
		}, {
			path: 'emailregistercodeDetail',
			component: emailregistercodeDetail
		}, {
			path: 'emailregistercodeAdd',
			component: emailregistercodeAdd
		}
		, {
			path: 'menuList',
			component: menuList
		}, {
			path: 'menuDetail',
			component: menuDetail
		}, {
			path: 'menuAdd',
			component: menuAdd
		}
		, {
			path: 'discussshangpinxinxiList',
			component: discussshangpinxinxiList
		}, {
			path: 'discussshangpinxinxiDetail',
			component: discussshangpinxinxiDetail
		}, {
			path: 'discussshangpinxinxiAdd',
			component: discussshangpinxinxiAdd
		}
		, {
			path: 'discusstehuihuodongList',
			component: discusstehuihuodongList
		}, {
			path: 'discusstehuihuodongDetail',
			component: discusstehuihuodongDetail
		}, {
			path: 'discusstehuihuodongAdd',
			component: discusstehuihuodongAdd
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
